.node div {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0.5px solid black;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

.node .start-icon {
    z-index: 0;
    font-size: 1.7vi;
    animation: grow 0.3s ease-in-out forwards;
}

.node .end-icon {
    z-index: 0;
    font-size: 1.6vi;
    animation: grow 0.3s ease-in-out forwards;
}

.node .wall {
    background-color: black;
    animation: grow 0.3s ease-in-out forwards;
}

.node .visited {
    background-color: crimson;
    animation: grow 0.3s ease-in-out forwards,
               color-transition-visited 0.8s ease-in-out forwards;
}

.node .path {
    background-color: gold;
    animation: grow 0.3s ease-in-out forwards;
}

@keyframes grow {
    from {
        transform: scale(0.35);
    }
    to {
        transform: scale(1);
    }
}

@keyframes color-transition-visited {
    from {
        background-color: #251f1f;
    }
    to {
        background-color: crimson;
    }
}
